export default function App() {
    const [current, setCurrent] = useState('mail');

    useEffect(() => {
        // masterService.listBrands()
    }, []);
    const onClick = (e) => {
        console.log('click ', e);
        setCurrent(e.key);
    };
    return (
        <>
            <Row>
                <Col
                    xs={{ span: 0 }}
                    sm={{ span: 24 }}
                >
                    <Menu onClick={onClick} selectedKeys={[current]} mode="horizontal" items={items} className="horizintal-category-menu" />;
                </Col>
            </Row>
        </>
    );
};