import { Row, Col, Image, Input, Button, Modal, Drawer } from "antd";
import Logo from "../../Assets/logo.png";
import { Link } from "react-router-dom";
import { SearchOutlined, MenuOutlined } from "@ant-design/icons";
import { useState } from "react";
import User from "./User";
import Menu from "./Menu";


export default function TopHeader() {
    const [showMobileSearch, setShowMobileSearch] = useState(false);
    const [showMenu, setShowMenu] = useState(false);

    return (
        <>
            <Row justify="space-around" align="middle">
                {/* Deskdop View */}
                <>
                    <Col
                        xs={{ span: 0 }}
                        sm={{ span: 4 }}
                    >
                        <Link to="/">
                            <Image src={Logo} alt="amit-labs-logo" width="250px" preview={false} />
                        </Link>
                    </Col>
                    <Col
                        xs={{ span: 0 }}
                        sm={{ span: 10 }}
                    >
                        <SearchBox />
                    </Col>
                    <Col
                        xs={{ span: 0 }}
                        sm={{ span: 4 }}
                    >
                        <User />
                    </Col>
                </>

                {/* Mobile View */}
                <>
                    <Col
                        xs={{ span: 12 }}
                        sm={{ span: 0 }}
                    >
                        <Link to="/">
                            <Image src={Logo} alt="amit-labs-logo" width="150px" preview={false} />
                        </Link>
                    </Col>
                    <Col
                        xs={{ span: 2 }}
                        sm={{ span: 0 }}
                    >
                        <Button type="primary" icon={<SearchOutlined />} onClick={() => { setShowMobileSearch(true) }} />
                    </Col>
                    <Col
                        xs={{ span: 6 }}
                        sm={{ span: 0 }}
                    >
                        <User />
                    </Col>
                    <Col
                        xs={{ span: 2 }}
                        sm={{ span: 0 }}
                    >
                        <Button type="primary" icon={<MenuOutlined />} onClick={() => { setShowMenu(true) }} />
                        <MenuModal {...{ showMenu, setShowMenu }} />
                    </Col>
                </>
            </Row>
            <SearchModal {...{ showMobileSearch, setShowMobileSearch }} />


        </>
    );
};

function SearchModal({ showMobileSearch, setShowMobileSearch }) {
    return (
        <>
            <Modal closeIcon={<></>} open={showMobileSearch} footer={null} maskClosable={true} onCancel={() => { setShowMobileSearch(false) }}>
                <SearchBox />
            </Modal>
        </>
    );
}

function SearchBox() {
    return <Input.Search placeholder="Search your Product" enterButton className="app-gradient" allowClear />
}

function MenuModal({ showMenu, setShowMenu }) {
    return (
        <>
            <Drawer open={showMenu} footer={null} closable={true} onClose={() => { setShowMenu(false) }} width={300}>
                <Menu />
            </Drawer>
        </>
    );
}