import { ShareAltOutlined } from "@ant-design/icons";
import { Button, Card, Typography } from "antd";
import React from "react";
import moment from "moment";
const NewsCard = ({date,img,title,content}) => {
  return (
    <Card
      hoverable
      style={{
        width: "360px",
        height:'700px',
        background: "rgb(120 68 146)",
        border: "none",
      }}
      cover={
        <img
          alt="example"
          style={{ width: "100%",height:"280px",objectFit:"cover" }}
          src={img}
        />
      }
    >
      <div className="head">
        <Typography.Title level={3} style={{ color: "white" }}>
          {title}
        </Typography.Title>
        <ShareAltOutlined style={{ color: "white", fontSize: "1.8rem" }} />
      </div>
      <span style={{ color: "white" }}>{moment(date).format("dddd, MMMM Do YYYY")}</span>
      <h3 style={{ color: "white", fontWeight: "lighter" }}>
        {content}
      </h3>
      <div style={{ display: "flex", justifyContent: "end", }}>
        <Button style={{ color: "purple" }}>Read More </Button>
      </div>
    </Card>
  );
};

export default NewsCard;
