import { useSelector, useDispatch } from 'react-redux';
import { Button, Dropdown, Input, Modal, Row, Col, Image, Typography, message, Space } from "antd";
import { Link } from "react-router-dom";
import { UserOutlined, ShoppingCartOutlined, HeartFilled, HourglassOutlined } from "@ant-design/icons";
import { useState } from "react";
import loginImg from "../../Assets/login.jpg"
import userService from '../../service/user';
import { changeDetails } from '../../redux/user';



export default function User() {
    const user = useSelector((state) => state.user);
    const [showLoginModal, setShowLoginModal] = useState(false);
    const dispatch = useDispatch();

    const items = [
        {
            key: 'my-profile',
            icon: <UserOutlined />,
            label: <Link to="/">My Profile</Link>,
        },
        {
            key: 'orders',
            icon: <ShoppingCartOutlined />,
            label: <Link to="/">Orders</Link>,
        },
        {
            key: 'wishlist',
            icon: <HeartFilled />,
            label: <Link to="/">Wishlist</Link>,
        },
        {
            key: 'coupon',
            icon: <HourglassOutlined />,
            label: <Link to="/">Coupon</Link>,
        },
    ];
    return (
        <>
            {
                user.isLoggedin
                    ? <>
                        <Row>
                            <Col
                                xs={{ span: 0 }}
                                sm={{ span: 24 }}
                            >
                                <Row>
                                    <Col span={12}>
                                        <Dropdown menu={{ items }} >
                                            <Button className="app-gradient">{user.name || 'You'}</Button>
                                        </Dropdown>
                                    </Col>
                                    <Col span={12}>
                                        <Button className="app-gradient" icon={<ShoppingCartOutlined />}>Cart</Button>
                                    </Col>
                                </Row>
                            </Col>
                            <Col
                                xs={{ span: 24 }}
                                sm={{ span: 0 }}
                            >
                                <Row>
                                    <Col span={12}>
                                        <Dropdown menu={{ items }} >
                                            <Button type="primary" icon={<UserOutlined />} />
                                        </Dropdown>
                                    </Col>
                                    <Col span={12}>

                                        <Button type="primary" icon={<ShoppingCartOutlined />} style11={{ marginLeft: 5 }} />
                                    </Col>
                                </Row>
                            </Col>
                        </Row>
                    </>
                    : <>
                        <Button className="app-gradient" onClick={() => { setShowLoginModal(true) }} >Login</Button>
                        <LoginModal {...{ showLoginModal, setShowLoginModal, dispatch }} />
                    </>
            }

        </>
    );
}

const LoginModal = ({ showLoginModal, setShowLoginModal, dispatch }) => {

    const [data, setData] = useState({ otpSent: false, phone: '8826648669', otp: '0123' });

    const sendOtp = () => {
        userService.sendOtp({ phone: data.phone }).then(res => {
            message.info(res.message);
            setData({ ...data, otpSent: true });
        }).catch(err => {
            message.error(err.message);
        })
    }

    const verifyOtp = () => {
        userService.verifyOtp({ otp: data.otp, phone: data.phone }).then(res => {
            message.info(res.message);
            setData({ ...data, otpSent: false });
            setShowLoginModal(false);
            dispatch(changeDetails({ isLoggedin: true, name: res.data?.user?.name }));
        }).catch(err => {
            message.error(err.message)
        })
    }

    return (

        <Modal open={showLoginModal} width={700} onCancel={() => setShowLoginModal(false)} footer={null} maskClosable={true || !data.otpSent} closeIcon={<></>}>
            <Row gutter={[24, 8]}>
                <Col
                    className="app-gradient"
                    style={{ paddingTop: "23px" }}
                    xs={{ span: 24 }}
                    sm={{ span: 10 }}
                >
                    <Typography.Title level={1} style={{ color: 'white', margin: 0 }}>Login</Typography.Title>
                    <Typography.Title level={4} style={{ color: 'white' }}>Get access to your Order Wishlist and Recommendation</Typography.Title>
                    <Row>
                        <Col
                            xs={{ span: 0 }}
                            sm={{ span: 24 }}
                        >
                            <Image src={loginImg} preview={false} />
                        </Col>
                    </Row>
                </Col>

                <Col
                    xs={{ span: 24 }}
                    sm={{ span: 14 }}
                >
                    {!data.otpSent
                        ? <div style={{ display: "block", marginTop: "33px" }}>
                            <Input size="large" placeholder="Phone" className="phone-number-input" value={data.phone} onChange={(e) => { setData({ ...data, phone: e.target.value }) }} />
                            <Button className="app-gradient" type="primary" block size="large" onClick={sendOtp} htmlType="button" >Request OTP</Button>
                            <div style={{ display: "flex", justifyContent: "center" }}>
                                <Typography.Text>New to Amit Lab?</Typography.Text>
                                <Typography.Text strong>Create Account?</Typography.Text>
                            </div>
                        </div>
                        : <div style={{ display: "block", marginTop: 50 }}>
                            <div style={{
                                display: "flex", justifyContent: "center", flexDirection: "column", alignItems: "center", marginBottom: "39px", fontSize: "20px",
                                fontWeight: "bold"
                            }}>
                                <div>Please enter the OTP sent to</div>
                                <div strong>
                                    {data.phone}
                                    <Button type="text" className="primary-color" onClick={e => { setData({ ...data, otpSent: false }) }}>Change</Button>
                                </div>
                            </div>
                            <Input
                                size="large"
                                placeholder="OTP"
                                className="validate-otp-input"
                                value={data.otp} onChange={(e) => { setData({ ...data, otp: e.target.value }) }}
                            />
                            <Button className="app-gradient" type="primary" block size="large" onClick={verifyOtp}>
                                Verify
                            </Button>
                        </div>}
                </Col>
            </Row>
        </Modal>
    )
};