import { BrowserRouter } from 'react-router-dom';
import { ConfigProvider } from 'antd';
import Layout from './layout';
import './App.css';

function App() {
	return (
		<>
			<ConfigProvider theme={{
				token: {
					colorPrimary: "#774F99",
					borderRadius: '0px',
				}
			}} >
				<BrowserRouter basename="/amit-labs">
					<Layout />
				</BrowserRouter>
			</ConfigProvider>
		</>
	);
}

export default App;
