import { Col, Row, } from "antd";
import React from "react";
import NewsCard from "../../components/NewsCard";
import { Button } from "antd";
import { LeftOutlined, RightOutlined } from "@ant-design/icons";
import Slider from "react-slick";
const Blog = ({blogData}) => {
const slider = React.useRef(null)
    const settings = {
        dots: true,
        infinite: true,
        arrows:false,
        speed: 500,
        slidesToShow: 3,
        slidesToScroll: 1,

        responsive: [
            {
              breakpoint: 1424,
              settings: {
                slidesToShow: 3,
                slidesToScroll: 1,
                infinite: true,
                dots: true,
              },
            },
      
            {
              breakpoint: 1124,
              settings: {
                slidesToShow: 2,
                slidesToScroll: 1,
                infinite: true,
                dots: true,
              },
            },
            {
              breakpoint: 800,
              settings: {
                slidesToShow: 1,
                slidesToScroll: 1,
              },
            },
          ],
      
      };


    return (

        <>
            <Row style={{ marginTop: 20 }}>

            <Col
                    lg={{ span: 20 }}
                    style={{ margin: '0 auto' }}
                >
             <Slider ref={slider} {...settings}>
                {
                    blogData.map((v,i)=>(
                        <NewsCard
                        key={i}
                        date={v.date}
                        img={v.img?.url}
                        title={v.title}
                        content={v.description}
                    />
                    ))
                }
            </Slider>
            <div style={{ display: 'flex', marginTop: '40px', justifyContent: 'center', }}>
                <LeftOutlined onClick={() => slider.current.slickPrev()} style={{ fontSize: '2rem', color: 'white', fontWeight: 600,marginTop:'-70px' }} />
                <Button style={{ height: '40px', width: '150px', background: "white", color: "purple",marginTop:'15px' }}>
                    Subscribe
                </Button>
                <RightOutlined onClick={() =>slider.current.slickNext() } style={{ fontSize: '2rem', color: 'white', fontWeight: 600,marginTop:'-70px' }} />
            </div>
            </Col>

            </Row>
        </>
    )
};

export default Blog;
